.gradient-text {
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  background-image: linear-gradient(#83888f, #fefcf7);
  background-repeat: no-repeat;
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
}

.step__items {
  gap: 9.5rem;
  margin-bottom: 6.4rem;
  display: flex;
}

@media only screen and (width <= 480px) {
  .step__items {
    text-align: center;
    flex-direction: column;
    gap: 6rem;
  }
}

.step__item {
  position: relative;
}

.step__item:not(:last-child):before {
  content: "";
  border-top: 1px solid #fdd6ba;
  width: calc(100% + 9.5rem);
  position: absolute;
  top: -8rem;
}

@media only screen and (width <= 480px) {
  .step__item:not(:last-child):before {
    display: none;
  }
}

.step__item:after {
  content: "";
  background-color: #fefcf7;
  border: 1px solid #0e8784;
  border-radius: 500px;
  width: 3.1rem;
  height: 3.1rem;
  position: absolute;
  top: -9.5rem;
}

@media only screen and (width <= 480px) {
  .step__item:after {
    display: none;
  }
}

.step__item--plan:not(:last-child):before {
  left: 1.5rem;
}

.step__item--plan:after {
  background-color: #0000;
}

.step__number {
  margin-bottom: 3.8rem;
  display: inline-block;
}

.step__title {
