    line-height: 3.2rem;
  }

  .step__paragraph {
    max-width: 38ch;
    font-size: 1.5rem;
    line-height: 2.5rem;
  }
}

.container {
  max-width: 120rem;
  margin-top: 10rem;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.contato__dados {
  background-color: #333d4b;
  border-radius: 5px 0 0 5px;
}

.contato__form {
  border-radius: 0 5px 5px 0;
}

.contato__dados, .contato__form {
  padding: 6rem;
  box-shadow: 0 1px 10px #0000004d;
}

.contato__dados {
  flex-basis: 40%;
}

.contato__dados h2 {
  margin-bottom: 7rem;
}

.contato__form {
  flex-basis: 60%;
}

.contato__endereco {
  background: url("local.c554a060.svg") 0 8px no-repeat;
  margin-bottom: 3.5rem;
  padding-left: 3rem;
  font-size: 1.6rem;
}

.contato__endereco p:not(:last-child) {
  margin-bottom: 1rem;
}

.contato__canais a {
  margin-bottom: 1rem;
  padding-left: 3rem;
  font-size: 1.6rem;
  display: block;
}

.contato__canais a:first-child {
  background: url("email.039d964f.svg") 0 8px no-repeat;
}

.contato__canais a:last-child {
  background: url("telefone.6d3edc26.svg") 0 8px no-repeat;
}

.contato__canais a:nth-child(2) {
  margin-bottom: 3.5rem;
}

.contato__canais {
  margin-bottom: 6rem;
}

.contato__redes {
  align-items: center;
  gap: 5rem;
  padding-left: 3rem;
  display: flex;
}

.form {
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  display: grid;
}

.form label:not(p ~ label) {
  color: #333d4b;
  margin-bottom: 1rem;
  font: 900 2rem / 3.2rem Fraunces, sans-serif;
  display: block;
}

.form input:not(p ~ input), .form textarea {
  background-color: #f7f7f7;
  border: 1px solid #ededed;
  border-radius: 5px;
  width: 100%;
  padding: 1.2rem;
  font: 400 1.6rem / 3.2rem Barlow, sans-serif;
}

.contato__radio p {
  color: #333d4b;
  margin-bottom: 1rem;
  font: 600 2rem / 3.2rem Fraunces, sans-serif;
}

.contato__radio label {
  margin-right: 1.5rem;
  padding: 2rem;
  font: 400 1.7rem / 3.2rem Barlow, sans-serif;
  display: inline-block;
}

.contato__radio label:before {
  content: "";
  border: 1px solid #000;
  border-radius: 50%;
  width: 1.6rem;
  height: 1.6rem;
  margin-right: 1rem;
  display: inline-block;
}

.contato__radio input:checked + label:before {
  box-shadow: inset 0 0 0 4px #fff, inset 0 0 0 6px #000;
}

.contato__radio input {
  opacity: 0;
  pointer-events: none;
  position: absolute;
}

.contato__radio input:checked + label {
  color: #fefcf7;
  background: #0e8784;
  border-radius: 5px;
}

.botao {
  color: #fefcf7;
  text-transform: uppercase;
  cursor: pointer;
  background: #0e8784;
  border: none;
  border-radius: 5px;
  max-width: max-content;
  padding: 1.6rem 3.2rem;
  font: 600 1.8rem / 2.5rem Manrope, serif;
  display: inline-block;
}

.contato + footer {
  margin-top: 10rem;
}

@media (width <= 600px) {
  .container {
    flex-direction: column;
    margin-top: 0;
  }

  .contato__dados {
    border-radius: 0;
  }

  .contato__dados, .contato__form {
    box-shadow: initial;
  }

  .form, .contato__radio {
    flex-direction: column;
    display: flex;
  }

  .contato + footer {
    margin-top: 5rem;
  }
}

